.branding:after {
  content: '';
  display: table;
  clear: both; }

.branding__slogan {
  position: relative;
  z-index: 1;
  width: 210px;
  margin-top: -7px;
  margin-left: auto;
  margin-right: auto;
  font-size: 9px;
  line-height: 13px;
  text-transform: lowercase;
  text-align: right;
  color: #71e1ed;
  text-align: right; }

@media (min-width: 480px) {
  .branding--default {
    width: 350px;
    margin: 0 auto; } }

@media (min-width: 1460px) {
  .branding--default {
    width: 460px; } }

.branding--default .branding__logo {
  display: block;
  text-align: center; }
  .branding--default .branding__logo img {
    width: 210px; }
  @media (min-width: 480px) {
    .branding--default .branding__logo {
      float: left;
      width: 210px;
      margin-right: 10px; }
      .branding--default .branding__logo img {
        width: auto; } }
  @media (min-width: 1460px) {
    .branding--default .branding__logo {
      width: 290px; } }

.branding--default .branding__slogan span {
  display: inline-block;
  width: 130px; }

@media (min-width: 480px) {
  .branding--default .branding__slogan {
    float: left;
    width: 130px;
    text-align: left;
    margin-top: 18px;
    font-size: 10px;
    line-height: 14px; } }

@media (min-width: 1460px) {
  .branding--default .branding__slogan {
    font-size: 14px;
    line-height: 19px;
    width: 160px;
    margin-top: 24px; }
    .branding--default .branding__slogan span {
      width: auto; } }

.branding--compact {
  padding-left: 20px;
  padding-bottom: 10px;
  margin-bottom: 20px;
  border-bottom: 1px solid #777777; }
  .branding--compact .branding__logo {
    float: left;
    width: 130px;
    margin-right: 5px; }
  .branding--compact .branding__slogan {
    float: left;
    width: 100px;
    text-align: left;
    padding-top: 17px;
    line-height: 9px; }

.branding--search {
  padding-bottom: 15px;
  margin-bottom: 20px;
  width: 235px;
  margin-left: auto;
  margin-right: auto; }
  @media (min-width: 768px) {
    .branding--search {
      width: 355px; } }
  .branding--search .branding__logo {
    float: left;
    width: 130px;
    margin-right: 5px; }
    @media (min-width: 768px) {
      .branding--search .branding__logo {
        width: 210px;
        margin-right: 10px; } }
  .branding--search .branding__slogan {
    float: left;
    width: 100px;
    text-align: left;
    margin-top: 10px;
    line-height: 9px; }
    @media (min-width: 768px) {
      .branding--search .branding__slogan {
        width: 130px;
        font-size: 10px;
        line-height: 14px;
        margin-top: 18px; } }



