.shipping {
  position: relative;
  padding-right: 30px; }
  .shipping__target {
    float: left;
    width: 20px;
    line-height: 16px;
    color: #71e1ed; }
  .shipping__city {
    padding-left: 20px;
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 4px;
    color: #777777; }
  .shipping__period {
    padding-left: 20px;
    font-size: 10px;
    line-height: 12px;
    color: #bdbdbd;
    margin-bottom: 2px; }
  .shipping__select {
    padding-left: 20px;
    font-size: 10px;
    line-height: 15px;
    color: #bdbdbd; }
    .shipping__select span {
      border-bottom: 1px dashed; }
  .shipping__arrow {
    position: absolute;
    right: 0;
    top: -5px;
    color: #bdbdbd; }



