.options {
  position: relative;
  overflow: hidden;
  margin-bottom: 10px; }
  @media (min-width: 768px) {
    .options {
      height: 40px;
      padding-top: 10px;
      border-bottom: 1px solid #bdbdbd;
      margin-bottom: 20px; } }
  @media (min-width: 1460px) {
    .options {
      height: 50px;
      padding-top: 15px;
      margin-bottom: 20px; } }



