.search__toggle {
  display: block;
  width: 27px;
  height: 27px;
  line-height: 25px;
  background: #71e1ed;
  color: #fff;
  text-align: left;
  text-decoration: none; }
  .search__toggle:before {
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
