    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
  @media (max-width: 1459px) {
    .search__toggle {
      text-indent: -9999px;
      position: relative; }
      .search__toggle:before {
        position: absolute;
