.product_list {
  padding: 4px; }
  .product_list:after {
    content: '';
    display: table;
    clear: both; }
  .product_list__item {
    float: left;
    width: 50%;
    padding: 4px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s; }
    .product_list__item:nth-child(2n-1) {
      clear: left; }
    @media (max-width: 767px) {
      .product_list--single .product_list__item {
        display: none;
        position: relative;
        z-index: 2; }
        .product_list--single .product_list__item.active {
          display: block;
          width: 100%; } }
    @media (min-width: 480px) {
      .product_list__item {
        width: 33.33333%; }
        .product_list__item:nth-child(2n-1) {
          clear: none; }
        .product_list__item:nth-child(3n+1) {
          clear: both; } }
    @media (min-width: 768px) {
      .product_list__item {
        margin-bottom: 20px; } }
    @media (min-width: 1460px) {
      .product_list__item {
        margin-bottom: 60px; } }
  .product_list__placeholder {
    text-align: center; }
    @media (max-width: 479px) {
      .product_list__placeholder {
        float: left;
        width: 50%;
        padding: 4px; }
        .product_list__placeholder span {
          display: block;
          padding: 70px 10px;
          text-align: center; }
        .product_list--single .product_list__placeholder {
          display: none; } }
    @media (min-width: 480px) {
      .product_list__placeholder {
        clear: both;
        background: #bdbdbd;
        padding: 5px 0; } }
    @media (min-width: 1024px) {
      .product_list__placeholder {
        display: none; } }



