.locations {
  color: #777777; }
  .locations:after {
    content: '';
    display: table;
    clear: both; }
  @media (min-width: 768px) {
    .locations {
      padding-top: 18px; } }
  @media (min-width: 1024px) {
    .locations {
      padding-top: 14px; } }
  @media (min-width: 1460px) {
    .locations {
      padding-top: 21px; } }
  .locations__phone {
    float: right;
    width: 40%;
    text-align: right;
    font-size: 14px;
    line-height: 20px; }
    @media (min-width: 480px) {
      .locations__phone {
        font-size: 20px; } }
    @media (min-width: 768px) {
      .locations__phone {
        float: left;
        width: 45%;
        font-size: 20px;
        line-height: 30px;
        margin-right: 5%; } }
    @media (min-width: 1024px) {
      .locations__phone {
        font-size: 28px;
        line-height: 36px; } }
    @media (min-width: 1460px) {
      .locations__phone {
        font-size: 45px;
        line-height: 1;
        text-align: left;
        width: 330px;
        margin-right: 20px; } }
  .locations__shipping {
    float: left;
    width: 60%;
    font-size: 12px;
    line-height: 20px; }
    @media (min-width: 768px) {
      .locations__shipping {
        width: 50%;
        font-size: 10px;
        line-height: 14px; } }
    @media (min-width: 1024px) {
      .locations__shipping {
        font-size: 12px;
        line-height: 18px; } }
    @media (min-width: 1460px) {
      .locations__shipping {
        font-size: 14px;
        line-height: 18px;
        padding-top: 4px;
        width: auto; } }
  @media (max-width: 767px) {
    .locations__all {
      display: none; } }
  .locations__current {
    border-bottom: 1px dotted;
    cursor: pointer; }



