.order {
  position: relative;
  padding-right: 30px; }
  .order__cart {
    float: left;
    width: 20px;
    line-height: 16px;
    color: #71e1ed; }
  .order__order {
    padding-left: 20px;
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 4px;
    color: #777777; }
  .order__shipping {
    padding-left: 20px;
    font-size: 10px;
    line-height: 12px;
    color: #bdbdbd;
    margin-bottom: 2px; }
  .order__arrow {
    position: absolute;
    right: 0;
    top: 50%;
