.catalog_browser > ul > li {
  position: relative;
  margin-bottom: 20px; }
  .catalog_browser > ul > li > a {
    display: block;
    font-size: 16px;
    line-height: 20px;
    text-decoration: none;
    color: #262624;
    margin-bottom: 15px; }
  .catalog_browser > ul > li > ul {
    margin-bottom: 20px; }
  .catalog_browser > ul > li li {
    margin-bottom: 5px; }

.catalog_browser .fa-angle-down {
  position: absolute;
  right: 0;
  top: -3px;
  color: #bdbdbd; }

.catalog_browser .fa-angle-right {
  position: absolute;
  right: 0;
  top: -3px;
  color: #bdbdbd; }



