.region {
  padding: 20px 10px; }
  .region--odd {
    background: #71e1ed; }
  .region--even {
    background: #74ecaf; }
  @media (min-width: 768px) {
    .region {
      background: none;
      padding-left: 5%;
      padding-right: 5%; } }
  @media (min-width: 1024px) {
    .region {
      padding: 0;
      margin-bottom: 100px; } }
  @media (min-width: 1460px) {
    .region {
      padding-bottom: 50px;
      padding-top: 50px;
      margin-bottom: 0;
      position: relative; } }
  @media (min-width: 1600px) {
    .region:before {
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      bottom: 0;
      width: 40px; }
    .region--odd:before {
      background: #71e1ed; }
    .region--even:before {
      background: #74ecaf; } }

.breadcrumbs {
  font-size: 10px;
  line-height: 12px;
  color: #777777;
  margin-bottom: 10px;
  padding: 0 10px; }
  .breadcrumbs li {
    display: inline;
    margin-right: 5px; }
    .breadcrumbs li:after {
      margin-left: 5px;
      content: '>>'; }
    .breadcrumbs li:last-child:after {
      display: none; }
    .breadcrumbs li a {
      color: #777777;
      text-decoration: none; }
  @media (min-width: 1024px) {
    .breadcrumbs {
      padding-left: 5%;
      padding-right: 5%; } }
  @media (min-width: 1460px) {
    .breadcrumbs {
      width: 1460px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 0;
      padding-right: 0; } }



