@media (min-width: 1024px) {
  .category {
    padding-left: 5%;
    padding-right: 5%; } }

@media (min-width: 1460px) {
  .category {
    width: 1460px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0; } }

.category__header {
  margin: 0 0 10px;
  text-transform: uppercase;
  font-size: 21px;
  line-height: 24px; }
  @media (min-width: 768px) {
    .category__header {
      font-size: 40px;
      line-height: 1;
      margin-bottom: 25px; }
