    margin-bottom: 15px; }
  .category__highlight_text {
    font-weight: bold;
    font-size: 16px;
    line-height: 20px; }
    .category__highlight_text li {
      margin-bottom: 15px; }
  .category__lead {
    font-size: 18px;
    line-height: 24px;
    font-weight: 300; }
    .category__lead p {
      margin-bottom: 24px; }
  .category__info {
